.Navbar-header {
  width: 100%;
  position: fixed;
  top: 0;
  text-align: center;
  font-size: calc(10px + 2vmin);
  background-color: #fafafa;
}

.navbar {
  max-height: 70px;
}

.Custom-font {
  font-family: "MyFont";
  margin-left: 0.5em;
}

@font-face {
  font-family: "MyFont";
  src: local("MyFont"), url("../../fonts/Billabong.woff") format("woff");
}

.Navbar-header a {
  font-size: 0.4em;
}

.Navbar-custom {
  background-color: white;
}

.Spacer {
  flex: 0.25;
}
